<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

    <section class="van-doc-demo-section">
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">

        <van-row class="detail_title">
          <van-col span="8" offset="8">买价(bp)</van-col>
          <van-col span="8">卖价(bp)</van-col>
        </van-row>

        <van-collapse v-model="activeNames">
          <van-collapse-item :title="item.title" :name="index" v-for="(item,index) in list">
            <van-row class="detail_item" v-for="item2 in item.value">
              <van-col span="8">0.5Y</van-col>
              <van-col span="8">32.00</van-col>
              <van-col span="8">32.00</van-col>
            </van-row>
          </van-collapse-item>
        </van-collapse>

      </van-pull-refresh>
    </section>

  </div>
</template>

<script>
  export default {
    name: "creditDefaultSwap",
    data() {
      return {
        title: '信用违约互换',
        isLoading: false,
        activeNames: [0, 1, 2, 3, 4],
        list: [
          {title: '超AAA', value: [1,2,3,4]},
          {title: '普通AAA', value: [1,2,3,4]},
          {title: 'AA+', value: [1]},
          {title: 'AA', value: [1,2,3,4]},
        ]
      };
    },
    methods:{
      onClickLeft(){
        this.$router.back();
      },
      onRefresh() {
        setTimeout(() => {
          this.$toast('刷新成功');
          this.isLoading = false;
          this.count++;
        }, 500);
      }
    }
  }
</script>

<style scoped>

  .detail_title {
    background: rgba(255, 255, 255, 1);
    margin-bottom: 10px;
  }
  .detail_title div {
    font-size: 15.5px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(117, 117, 117, 1);
    line-height: 40px;
  }

  .detail_item div {
    font-size: 14px;
    font-family: PingFangSC-Regular;
    font-weight: 400;
    color: rgba(221, 169, 68, 1);
    line-height: 20px;

  }
  .detail_item div:first-child {
    color: rgba(124, 124, 125, 1);
  }

</style>



